import {Children} from "react";
import "./TText.css";
import {StyledP} from "./StyledP";

export default function TText({children, css, light, size, onClick, style}) {
    return (
        <StyledP css={{
            fontFamily: "DIN Condensed",
            fontWeight: light ? "300" : "normal",
            fontSize: `${size}px`,
            lineHeight: "100%",
            cursor: onClick ? "pointer" : "",
            ...css,
            ...style
        }} className={light ? "ttext-light" : "ttext"} onClick={onClick ? onClick : () => {}}
        >{Children.map(children, child => (child))}</StyledP>
    )
}