import FullContainer from "../../Common/FullContainer";
import BlockTitle from "../BlockTitle";
import PosterCalendar from "./PosterCalendar/PosterCalendar";
import {Col, Container, Grid, Row, Spacer} from "@nextui-org/react";
import useWindowSize from "../../../hooks/useWindowSize";
import PosterShowCard from "./PosterShowCard";
import TContainer from "../../Common/TContainer/TContainer";
import VerticalSlider from "../../Common/Slider/VerticalSlider";
import useValue from "../../../hooks/useValue";

export default function Poster() {
    const windowSize = useWindowSize();
    const responsiveControlWidth = 960;
    const calendarHeaderHeight = useValue(70);
    const m = [1,2,3];
    return (
        <FullContainer>
            <BlockTitle>Афиша</BlockTitle>
            <TContainer css={{h: "85%"}}>
                {windowSize.width >= responsiveControlWidth &&
                    <Row css={{h: "100%"}} justify={"space-between"}>
                        <Col css={{position: "relative", h: "100%"}}>
                            <PosterCalendar clbHeaderHeight={calendarHeaderHeight.set}/>
                        </Col>
                        <Spacer x={2}/>
                        {/*<Col>*/}
                            <Row css={{h: "100%", w: "min-content"}}>
                                <Col css={{h: "100%", w: "min-content"}}>
                                    <div style={{height: `${calendarHeaderHeight.value}px`, width: "10px"}}/>
                                    <div style={{height: `calc(100% - ${calendarHeaderHeight.value}px)`}}>
                                        <VerticalSlider items={m.map(item => (<PosterShowCard/>))}/>
                                    </div>
                                </Col>
                                <Col>

                                </Col>
                            </Row>
                        {/*</Col>*/}
                    </Row>
                }

                {windowSize.width < responsiveControlWidth &&
                    <Col css={{h: "100%", pb: "20px"}}>
                        <Row css={{position: "relative", h: "70%"}}>
                            <PosterCalendar/>
                        </Row>
                        <Row css={{h: "30%"}}>
                            <div style={{height: "100%", width: "100%", background: "whitesmoke"}}/>
                        </Row>
                    </Col>
                }
            </TContainer>
        </FullContainer>
    )
}