import {Row, Grid, Text, Col} from "@nextui-org/react";
import moment from "moment";
import TText from "../../../Common/TText";

export default function PosterCalendarDays({size, height, currentMonth, currentYear, currentDay, onSelectDay,
                                           onlyActive, activeDaysForCurrentMonth}) {

    const responsiveSize = () => {
        switch (size) {
            case "m":
                return {
                    headerHeight: 50,
                    fontSize: "24px",
                    selectedFontSize: "30px"
                }
            default:
                return {
                    headerHeight: 70,
                    fontSize: "48px",
                    selectedFontSize: "96px"
                }
        }
    }

   const dayCells = () => {
        let days = [];
        let isLastWeek = false;
        for (let idx = 0; idx < moment().month(currentMonth.value).year(currentYear.value).daysInMonth(); idx++) {
            const day = idx + 1;
            const isActive = activeDaysForCurrentMonth.includes(day);
            const date = moment().month(currentMonth.value).year(currentYear.value).date(day);
            const lastWeekStart = moment().month(currentMonth.value).year(currentYear.value).date(day).endOf("month").startOf("week");
            if (!isLastWeek && date.date() === lastWeekStart.date()) {
                isLastWeek = true;
            }
            days.push((
                <Grid xs={12/7} justify={"center"} alignItems={"center"} alignContent={"center"}
                      css={{cursor: isActive ? "pointer" : "", borderBottom: isLastWeek ? "" : "1px solid rgb(0,0,0,0.1)"}}
                      onClick={isActive ? () => onSelectDay(day) : () => {}}
                >
                    <Row css={{height: responsiveSize().selectedFontSize}} justify={"center"} align={"center"}>
                        <TText light css={{
                            transition: "200ms",
                            color: isActive ?
                                    day === currentDay.value ? "#8F2512" : "black"
                                    : "rgb(0,0,0,0.2)"
                            ,
                            fontSize: isActive ?
                                        day === currentDay.value ?
                                            responsiveSize().selectedFontSize : responsiveSize().fontSize
                                        : responsiveSize().fontSize}}
                        >{day}</TText>
                    </Row>
                </Grid>
            ))
        }
        const firstDayOfWeek = moment().date(1).month(currentMonth.value).year(currentYear.value).weekday();
        const append = [];
        for (let idx = 0; idx < firstDayOfWeek; idx++) {
            append.push(<Grid xs={12/7} css={{borderBottom: "1px solid rgb(0,0,0,0.1)"}}></Grid>)
        }

        days = [...append, ...days]
            return days;
    }

   return (
       <Row css={{h: `${height - 2 * responsiveSize().headerHeight}px`}}>
           <Grid.Container css={{h: "100%"}}>
               {dayCells().map(day => (day))}
           </Grid.Container>
       </Row>
   )
}